@DragDropContext(HTML5Backend)
class App extends React.Component {
    props: {appState: AppState, dispatch: Dispatch};
    public render() {
        const {appState, dispatch} = this.props;
        const menuFunction = this.props.appState.uiState.menuFunction || missionScreen;

        const missionSequence = [
            {
                description: "Unexpected Visitors",
                mission: "configs/mission1.6x6.yaml"
            },
            {
                description: "Deeper into Quarry",
                mission: "configs/mission2.6x6.yaml"
            }
        ];
        const initialDeck = [
            "Conscript",
            "Conscript",
            "Peacat",
            "Peacat",
            "Cave-in",
            "Burly Miner",
            "Burly Miner",
            "Core Pieces",
            "Core Pieces",
            "Larent Elite",
            "Larent Elite",
            "Fleeting Fame",
            "Fleeting Fame"
        ];
        const rewards = [
            [
                "Flamey Puff",
                "Flamey Puff",
            ]
        ];
        // TODO Allow casting spells using dragging always, even for spells without targets
        // TODO combine the core into one card?
        // TODO opposing player should show what they're doing more
        // TODO "next mission" should be centred
        // TODO different colour illuminations for card types?
        const decks = [initialDeck];
        for (let i = 0; i < rewards.length; i++) {
            decks.push(decks[i].concat(rewards[i]));
        }
        return menuFunction();

        // function mainMenu() {
        //     return <MainScreen clickCatArmy={() => dispatch(actions.setUIMenuFunction(catMissionScreen))}/>;
        // }
        function startMission(idx) {
            startGameWithMission(missionSequence[idx].mission, decks[idx]);
            dispatch(actions.setUIMenuFunction(gameState(restart, next)));
            return;
            function restart() {
                startMission(idx);
            }
            function next() {
                if (missionSequence.length > idx + 1) {
                    startMission(idx + 1);
                }
            }
        };
        function missionScreen() {
            return <MissionScreen startMission={startMission} missionSequence={missionSequence}/>
        }
        function gameState(restart, next) {
            return () => {
                //style={{position: "fixed", top: "50%", left: "50%"}}
                return <div style={{transform: "translate(50%, 70%)", height: "700px"}}>
                    <GameUIContainer x={0} y={0} restart={restart} next={next}/>
                </div>;
            }
            // function switchToMainMenu() {
            //     return dispatch(actions.setUIMenuFunction(missionScreen));
            // }
        }
    }
}

export const AppContainer = (connect as any)(
    appState => ({appState}), 
    dispatch => ({dispatch}))(App);



