import { initEngineWorker, getState, GameState, getNextAIAction, GameAction, getActions, getAscii, getAnimationStates, GameInitData, initGame, GameActionNode } from "../../game-logic/engine";
import { makeActions, makeDispatchActions, assign, append } from "../../jsUtils";
import * as uiStateActions from './uiStateActions';
import { GlobalGameState } from "./gameStateReducer";

function _ensureGameStateConsistency(gameState: GameState) {
    let uuids = {};
    for (let zone of [
            gameState.player1.hand, 
            gameState.player2.hand]) {
        for (let {uuid} of zone) {
            if (uuids[uuid]) {
                throw new Error();
            }
            uuids[uuid] = true;
        }
    }
}

export const ACTION_FUNCS = {
    setGameState(state:GlobalGameState, gameState:GameState, legalActions: GameActionNode[]) {
        let {gameStateHistory, gameState: oldState} = state;
        if (oldState) {
            gameStateHistory = append(gameStateHistory, oldState);
        }
        _ensureGameStateConsistency(gameState);
        return assign(state, {gameState, gameStateHistory, legalActions})
    }
    // pushAnimationStates(state:GlobalGameState, animationStates:GameState[], legalActions: GameAction[]) {
    //     let {gameStateHistory, gameState: oldState} = state;
    //     if (oldState) {
    //         gameStateHistory = append(gameStateHistory, oldState);
    //     }
    //     return assign(state, {gameStateHistory, legalActions})
    // },
    // popAnimationState(state:GlobalGameState) {
    //     let {gameAnimationQueue} = state;
    //     if (gameAnimationQueue.length === 0) {
    //         _currentlyAnimating = false;
    //         return state;
    //     }
    //     // state = ACTION_FUNCS.setGameState(state, gameAnimationQueue[0])
    //     gameAnimationQueue = gameAnimationQueue.slice(1);
    //     return assign(state, {gameAnimationQueue});
    // }
};

export const actions = makeActions(ACTION_FUNCS);
export const toDispatchActions = dispatch => makeDispatchActions(dispatch, actions);

export async function executeGameAction(dispatch, action) {
    await action.execute();
    return await syncState(dispatch);
}
export async function syncState(dispatch) {
    console.log("GET ACTIONS")
    let gameActions = await getActions();
    console.log({gameActions})
    let state = await getState();
    console.log({state})
    dispatch(actions.setGameState(state, gameActions));
    return state;
}

export async function gameAIMove(dispatch) {
    let action = await getNextAIAction();
    return await executeGameAction(dispatch, action);
}

export async function initGameAndDispatchState(dispatch, scenarioFile, deck) {
    await initEngineWorker();
    await initGame(scenarioFile, deck);
    let state = await getState();
    let gameActions = await getActions();
    dispatch(actions.setGameState(state, gameActions));
    dispatch(uiStateActions.actions.assign({
        openInformation: false //gameInitData.openInformation
    }));
    // let animationStates = await getAnimationStates();
    // queueAnimationSet(dispatch, animationStates);
}

let _currentlyAnimating = false;
let _animationSet:GameState[] = [];
export async function queueAnimationSet(dispatch, animationStates: GameState[]) {
    _animationSet = _animationSet.concat(animationStates);
    
    function popAnimationState() {
        _animationSet
    }
}



