import * as uiStateActions from './uiStateActions'
import { hasProperty } from '../../jsUtils';
import { GameActionTargets } from '../../game-logic/engine';

export type Dispatch = (action:any) => void;

export interface CardUIState {
    // Set if the card was hovered over. Shows which zone was hovered over.
    hoverZone?: string;
    // x & y are interpolated by react-motion, not stored here
    // rotation is interpolated, not stored here
}

export interface UIState {
    menuFunction: any;
    povPlayerId: number;
    draggingUuid: number;

    // targets selected during a compound action
    actionState: GameActionTargets;
    cardUIStates: {[uuid:number]: CardUIState|undefined};
    openInformation: boolean;

}

const initialState:UIState = {
    menuFunction: null,
    actionState: {sources: [], targets: []}, povPlayerId: 0, 
    cardUIStates: {}, draggingUuid: 0,
    openInformation: true
};

export function uiStateReducer(state:UIState=initialState, action): UIState {
    if (hasProperty(uiStateActions.ACTION_FUNCS, action.type)) {
        return uiStateActions.ACTION_FUNCS[action.type](state, ...action.args);
    }
    return state;
}



