import {Comlink} from 'comlink';
import documentReady from '@awaitbox/document-ready'
import { onKeyPress } from './keypressUtils';
import { GAME_STORE } from '../index';

let AIClass:any = Comlink.proxy(new Worker('diviniWorker.js'));
let ai:any = null;

export interface GameSquareInitData {
    card: string;
    x, y, ownerId, contents: number;
}

export interface GameInitData {
    decks: string[][];
    squareData: GameSquareInitData[];
    playerControllers: any[];
    openInformation?: boolean;
 }

export async function initEngineWorker() {
    if (ai != null) {
        await ai.reset();
        return;
    }
    await documentReady();
    ai = await new AIClass();
    await ai.initGameData();
}

export async function initGame(scenarioFileName, deck) {
    await ai.startGame(scenarioFileName, JSON.stringify(deck));
}

// START ACTION WRAPPING

export interface GameActionSource {
   type: string;
   source: any;
}

export interface GameActionTarget {
   type: string;
   target: any;
}

export interface GameActionTargets {
    sources: GameActionSource[];
    targets: GameActionTarget[];
    sourceUnit?: GameCard;
    sourceSquare?: GameCard;
    sourceHandCard?: GameCard;
    targetUnit?: GameCard;
    targetSquare?: GameCard;
    targetHandCard?: GameCard;
}

export interface GameAction extends GameActionTargets {
   undoMark?: number;
   index: number;
   type: string;
   hash: number;
   undo(): void;
   execute(): void;
}

export interface GameActionNode {
    action: GameAction;
    subactions?: GameActionNode[];
}

export interface GameActionResult {
    action: GameAction;
    score: number;
}

function _actionMethods(actionNode: GameActionNode) {
   let {action} = actionNode;
   let actionKeys = Object.keys(action);
   action.undo = async () => {
       if (!action.undoMark) {
           throw "Not executed!";
       }
       await ai.resetStateFromHistoryMark(action.undoMark);
       delete action.undoMark;
   };
   action.execute = async () => {
       if (action.undoMark) {
           throw "Already executed!";
       }
       action.undoMark = await ai.getStateHistoryMark();
       await ai.performActionByIndex(action.index);
   };
   action.targets = actionKeys.filter(k => k.startsWith("target")).map(type => ({type, target: action[type]}));
   action.sources = actionKeys.filter(k => k.startsWith("source")).map(type => ({type, source: action[type]}));
   if (actionNode.subactions) {
       let i = 0;
       for (const subaction of actionNode.subactions) {
            subaction.action.index = i++;
           _actionMethods(subaction);
       }
   }
}

export async function getActions(): Promise<GameActionNode[]> {
   let rawActions = JSON.parse(await ai.getActionJson());
   let i = 0;
   for (let action of rawActions) {
       action.action.index = i++;
       _actionMethods(action);
   }
   return rawActions; // Now augmented
}

// END ACTION WRAPPING

// START STATE WRAPPING

export interface GameCardStats {
   name, description, flavourText: string;
   traits: string[];
   imageUrl: string;
   uuid, cost, aiValue: number;
   isUnit: boolean;
}

export interface GameUnit extends GameCardStats {
   ownerId: number;
   x, y: number;
   getUnitPower, getUnitMaxPower: number;
   getUnitRange, getUnitDamage: number;
   isUnitReady: boolean;
   power, maxPower, range: number;
}

export interface GameCard extends GameUnit {
}

export interface GameSquare {
   x, y: number;
   unit?: GameUnit;
   contents?: string;
}

export type GameBoard = GameSquare[];

export interface GamePlayer {
   id: number;
   gold, fame: number;
   victoryPoints, targetVictoryPoints: number;
   lordCard: GameCard;
   deck, hand, graveyard: GameCard[];
}

export interface GameState {
   board: GameBoard;
   boardWidth, boardHeight: number;
   currentPlayerId: number;
   player1, player2: GamePlayer;
   gameIsWon: boolean;
   winnerPlayerId: number;
   // If a queued up game state:
   animationMs?: number;
}

export async function getState(): Promise<GameState> {
    return JSON.parse(await ai.getStateJson());
}

export async function getCardDefJson(): Promise<GameCard[]> {
    return JSON.parse(await ai.getCardDefJson())["value0"];
}
  
export async function getAnimationStates(): Promise<GameState[]> {
    let subJsons = JSON.parse(await ai.getAnimationStatesJson());
    return subJsons.map(JSON.parse);
 }
 
export async function getAscii(): Promise<string> {
    return await ai.ascii();
}

async function getRawGameActions(): Promise<GameActionNode[]> {
    return JSON.parse(await ai.getActionJson());
}

export async function getNextAIAction(): Promise<GameAction> {
    let actionResult:GameActionResult = JSON.parse(await ai.getNextAIAction());
    let action = actionResult.action;
    let actions = await getRawGameActions();
    // Reconcile action with index:
    for (let i = 0; i < actions.length; i++) {
        if (JSON.stringify(actions[i].action) === JSON.stringify(action)) {
            action.index = i;
            break;
        }
    }
    _actionMethods({action});
    return action;
}
// END STATE WRAPPING

export enum SquareContents {
    NONE = 0,
    P1_TEMPLE = 1,
    P2_TEMPLE = 2,
    P3_TEMPLE = 3,
    P4_TEMPLE = 4,
    SANCTUM = 5,
    OUTER_SANCTUM = 6
}
