import * as React from 'react';
import { startGameWithMission } from './Missions';

const styles: any = {
    container: {
        width: 200,
        height: 200,
        backgroundColor: '#77EB11',
        display: 'table',
    },
    paragraph: {
        textAlign: 'center',
        width: '100%',
        height: '100%',
        display: 'table-cell',
        verticalAlign: 'middle'
    },
};

export function MissionScreen({ startMission, missionSequence }) {
    return <>
        <p style={styles.paragraph}>
            <b>Chapter 1:</b> <br/>
            After a cave in, your party becomes stranded. Unexpected visitors emerge...  
        </p>
        {missionSequence.map(({ description, mission }, idx) => <>
            <button key={mission} type="button" className="btn" onClick={() => startMission(idx)}>
                {description}
            </button> <br />
        </>)}
    </>;
}

export function MainScreen({ clickCatArmy }) {
    return <>
        <p style={styles.paragraph}>
            Choose your team
      </p>,
        <button type="button" className="btn" onClick={clickCatArmy}>
            Cats
      </button>
    </>;
}

